header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden; 
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 10rem 10rem 0 0;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

.scroll_down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 1.2rem;
}

/* ========== MEDIA QUERIES =========== */
@media screen and (max-width: 1024px) {
    .header {
        height: 68vh;
    }
}

@media screen and (max-width: 600px) {
    .header {
        height: 100vh;
    }

    .scroll_down {
        display: none;
    }
}